<template>
  <div class="myFootprint">
    <div class="title1">
      <div class="back" @click="back"></div>
      {{title}}
    </div>
    <div class="tab_section1">
      <div class="tab_content" v-for="(item,index) of recommendArr" :key="index">
        <img @click="onSectionView(item)" :src="item.image" class="left" alt />
        <div class="right">
          <div @click="onSectionView(item)" class="title">
            <img :src="item.icon" alt />
            <span>{{item.title}}
              <em v-show="item.updateShow"></em>
            </span>
          </div>
          <p @click="onSectionView(item)">{{item.text}}</p>
          <div class="bottom">
            <img
              @click="onCollect(item)"
              :src="item.identifySub.shadeImg==false?require('../../assets/images/essay_btn_collect_gray@3x.png'):require('../../assets/images/btn_collect_selected@3x.png')"
              alt
            />
            <div @click="onLike($event,item)">
              <span v-show="likeFlag"></span>
            </div>
            <ol class="tel">{{item.sureCount}}</ol>
            <ul class="bo_r" @click="item.identifySub.trampleShow=true">
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </div>
        </div>
        <transition enter-active-class="animated zoomIn" leave-active-class="animated zoomOut">
          <div class="trample" v-show="item.identifySub.trampleShow" @click="item.identifySub.trampleShow=false">
            <!-- <p>
              <span>相似试图</span>
              <img src="../../assets/images/icon_tiaozhuanlb@3x.png" alt />
            </p> -->
            <p @click="item.identifySub.trampleShow = false,writeShow = true,secConObj=item">
              <span>其他意见</span>
              <img src="../../assets/images/icon_xieyijian@3x.png" alt />
            </p>
            <p @click="onTreadCause($event,item)">
              <span>不感兴趣</span>
              <img src="../../assets/images/icon_kulian@3x.png" alt />
            </p>
          </div>
        </transition>
      </div>
    </div>
    <van-overlay :show="writeShow" z-index="300">
      <div class="submitInfo" @click.stop>
        <van-field
          v-model="treadMsg"
          rows="6"
          autosize
          type="textarea"
          maxlength="100"
          placeholder="请输入具体意见描述"
          show-word-limit
        />
        <van-button
          type="primary"
          color="linear-gradient(270deg, #45A4B1 0%, #2A7875 100%)"
          block
          @click="onTreadIdea()"
        >提交</van-button>
        <img
          @click="writeShow = false,treadMsg = ''"
          src="../../assets/images/btn_close@1x.png"
          alt
        />
      </div>
    </van-overlay>
  </div>
</template>
<script>
import axios from 'axios';
const productionUrl = window.origin + '/mobile456/xxtx/py';
const instance = axios.create({
  baseURL: process.env.NODE_ENV === 'development' ? '/api/mobile' : productionUrl
});
export default {
  data() {
    return {
      recommendArr: [],
      secConObj: {},
      likeFlag: false,
      writeShow: false,
      treadMsg: "",
      userInfoObj: this.$store.state.userInfoObj || {},
      id: this.$route.query.id,
      title: this.$route.query.title,
      flag:false
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.getRecommendData();
    });
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    //更新显示红点
    getFootDataUpdate(arr) {
      this.$axios
        .get(`/reminder?aamid=${this.userInfoObj.aamid}`)
        .then(res => {
          if (res.data.errcode == "success" && res.status === 200) {
            let updateArr = [...res.data.data] || [];
            updateArr.forEach(i => {
              arr.forEach(item => {
                if (i === item.moduleid) {
                  item.updateShow = true;
                }
              });
            });
            setTimeout(()=>{
              this.recommendArr = arr
              this.flag = true
            },50)
          }
        });
    },
    //获取数据
    getRecommendData() {
      this.$axios
        .post("/hopeModule/getModuleByShortCutBar", {
          aamid: this.userInfoObj.aamid,
          deptid: this.userInfoObj.deptid,
          odeptid: this.userInfoObj.odeptid,
          shortname: this.title,
          moduleidArr: this.id
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            var recommendArrData = [...res.data.data] || [];
            this.getFootDataUpdate(recommendArrData);
            setTimeout(()=>{
              if(this.flag === false){
                this.recommendArr = recommendArrData
              }
            },500)
          }
        });
    },
    //section关注和取消关注
    onCollect(item) {
      item.identifySub.shadeImg = !item.identifySub.shadeImg;
      if (item.identifySub.shadeImg == false) {
        this.$toast({
          message: "取消关注",
          icon: require("../../assets/images/btn_collect_normal@2x.png")
        });
        this.$axios
          .post("/hopeModule/deleteWatchHopeModule", {
            aamid: this.userInfoObj.aamid,
            moduleid: item.moduleid
          })
      } else {
        this.$toast({
          message: "已关注",
          icon: require("../../assets/images/btn_collect_selected@2x.png")
        });
        this.$axios
          .post("/hopeModule/insertWatchHopeModule", {
            aamid: this.userInfoObj.aamid,
            moduleid: item.moduleid
          })
      }
    },
    //section点赞
    onLike(e,item) {
      this.likeFlag = true;
      e.target.className = this.likeFlag ? "like_animate" : "";
      var timer = setTimeout(() => {
        clearTimeout(timer);
        this.likeFlag = false;
        e.target.className = this.likeFlag ? "like_animate" : "";
      }, 600);
      item.sureCount++
      this.$axios
        .post("/hopeComments/insertHopeComments", {
          aamid: this.userInfoObj.aamid,
          moduleid: item.moduleid,
          logtime: Date.parse(new Date()),
          comments: 1,
          description: ""
        })
    },
    //section点踩原因
    onTreadCause(e, item) {
      item.identifySub.trampleShow = false;
      this.$toast({
        message:"意见已收到",
        icon: require("../../assets/images/succeed.png")
      });
      this.$axios
        .post("/hopeComments/insertHopeComments", {
          aamid: this.userInfoObj.aamid,
          moduleid: item.moduleid,
          logtime: Date.parse(new Date()),
          comments: 0,
          description: "不感兴趣"
        })
    },
    //section点踩意见
    onTreadIdea() {
      if (this.treadMsg) {
        this.writeShow = false;
        this.treadMsg = "";
        this.$toast({
          message:"提交成功",
          icon: require("../../assets/images/succeed.png")
        });
        this.$axios
          .post("/hopeComments/insertHopeComments", {
            aamid: this.userInfoObj.aamid,
            moduleid: this.secConObj.moduleid,
            logtime: Date.parse(new Date()),
            comments: 0,
            description: this.treadMsg
          })
      } else {
        this.$toast({
          message:"意见不能为空",
          icon: require("../../assets/images/fail.png")
        });
      }
    },
    //点击section跳转增加足迹
    onSectionView(item) {
      window.location.href = item.url
      if (item.updateShow === true) {
        instance.get(
          `/add-mark?aamid=${this.userInfoObj.aamid}&moduleid=${item.moduleid}&modulename=${item.title}`
        );
      }
      this.$axios
        .post("/hopeUserLog_h/insertUserLog_h", {
          aamid: this.userInfoObj.aamid,
          moduleid: item.moduleid,
          logtime: Date.parse(new Date())
        })
    }
  }
};
</script>
<style lang="less" scoped>
.myFootprint {
  width: 100%;
  background: #f1f1f1;
  overflow: hidden;
  .submitInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    height: 100%;
    padding: 1rem 0.2rem;
    box-sizing: border-box;
  }
  .title1 {
    width: 100%;
    height: 0.5rem;
    text-align: center;
    line-height: 0.5rem;
    color: #000;
    font-size: 0.15rem;
    background: #fff;
    padding: 0 0.5rem;
    box-sizing: border-box;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 100;
    .back {
      position: absolute;
      left: 0;
      top: 0;
      width: 0.4rem;
      height: 100%;
      background: url("../../assets/images/icon_return@2x.png") no-repeat center
        center;
      background-size: 0.2rem 0.2rem;
    }
  }
}
.tab_section1 {
  width: 100%;
  padding: 0 0.12rem;
  box-sizing: border-box;
  margin-bottom: 0.2rem;
  margin-top: .62rem;
  .tab_content {
    width: 100%;
    height: 1.15rem;
    background: #ffffff;
    margin-top: 0.16rem;
    padding: 0.12rem;
    box-sizing: border-box;
    position: relative;
    .trample {
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.5);
      position: absolute;
      left: 0;
      top: 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0.19rem 0.4rem;
      box-sizing: border-box;
      p {
        width: 1rem;
        height: 0.3rem;
        border-radius: 0.15rem;
        border: none;
        background: #fff;
        padding: 0 0.14rem;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        align-items: center;
        img {
          width: 0.16rem;
          height: 0.16rem;
        }
        span {
          font-size: 0.12rem;
          color: #232323;
        }
      }
    }
    .left {
      width: 0.95rem;
      height: 100%;
      float: left;
      border-radius: 0.05rem;
    }
    .right {
      width: 2.22rem;
      height: 100%;
      float: left;
      margin-left: 0.1rem;
      .title {
        width: 100%;
        height: 0.35rem;
        line-height: 0.35rem;
        font-size: 0.14rem;
        color: #3a3a3a;
        font-weight: bold;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        padding-left: 0.1rem;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        img {
          width: 0.18rem;
          height: 0.18rem;
          margin-right: 0.15rem;
        }
        span {
          position: relative;
          em {
            width: 0.05rem;
            height: 0.05rem;
            background: #ff3232;
            position: absolute;
            left: -0.08rem;
            top: 0.02rem;
            border-radius: 50%;
            box-shadow: 0rem 0rem 0.05rem 0rem rgba(242, 48, 48, 0.67);
          }
        }
      }
      p {
        width: 100%;
        height: 0.42rem;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
        font-size: 0.12rem;
        color: #969696;
        padding-left: 0.1rem;
        box-sizing: border-box;
      }
      .bottom {
        width: 100%;
        height: 0.18rem;
        padding-left: 0.1rem;
        box-sizing: border-box;
        img {
          width: 0.18rem;
          height: 100%;
          float: left;
        }
        .tel {
          width: 1rem;
          height: 100%;
          line-height: 0.18rem;
          float: left;
          margin-left: 0.02rem;
          font-size: 0.1rem;
          color: #a5a5a5;
        }
        div {
          width: 0.16rem;
          height: 0.16rem;
          background: url("../../assets/images/btn_good_normal_gray@3x.png")
            no-repeat center center;
          background-size: 0.2rem 0.2rem;
          float: left;
          margin-right: 0.05rem;
          margin-left: 0.22rem;
          position: relative;
          span {
            width: 100%;
            height: 100%;
            display: block;
            position: absolute;
            left: 0;
            top: 0;
          }
        }
        .like_animate {
          animation: like_animate 0.6s linear forwards;
        }
        @keyframes like_animate {
          0% {
            transform: rotate(0deg);
            background: url("../../assets/images/btn_good_selected@3x.png")
              no-repeat center center;
            background-size: 0.2rem 0.2rem;
          }
          25% {
            transform: rotate(-30deg);
          }
          50% {
            transform: rotate(0deg);
          }
          70% {
            transform: rotate(30deg);
          }
          100% {
            transform: rotate(0deg);
            background: url("../../assets/images/btn_good_normal_gray@3x.png")
              no-repeat center center;
            background-size: 0.2rem 0.2rem;
          }
        }
        .bo_r {
          width: 0.18rem;
          height: 100%;
          justify-content: space-between;
          display: flex;
          align-items: center;
          padding: 0 0.03rem;
          box-sizing: border-box;
          background: #fff;
          border: none;
          margin: 0;
          float: right;
          li {
            width: 0.02rem;
            height: 0.02rem;
            background: #d8d8d8;
          }
        }
      }
    }
  }
}
</style>